import { Link } from 'react-router-dom';
import { useState } from 'react';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import HomeIcon from '@material-ui/icons/Home';
import EditIcon from '@material-ui/icons/Edit';
import MapIcon from '@material-ui/icons/Map';
import SwapHorizIcon from '@material-ui/icons/SwapHoriz';
import { AppBar } from '@material-ui/core';
import PaletteIcon from '@material-ui/icons/Palette';
import history from '../history';
import React from 'react';

export default function SimpleBottomNavigation() {

    const pathname = window.location.pathname; // in case user visits the path directly. The BottomNavBar is able to follow suit.
    const [value, setValue] = useState(pathname);
    const handleChange = (event: any, newValue: any) => {
        console.log(event);

        setValue(newValue);
    };



    // const [value, setValue] = useState('/');
    // let currentPath = history.location.pathname
    // if (value !== currentPath)
    //     setValue(currentPath)
    return (
        <AppBar position="fixed" color="primary" style={{ top: "auto", bottom: 0 }}>
            <BottomNavigation
                value={value}
                onChange={handleChange}
                showLabels
            >
                location.pathname
                <BottomNavigationAction label="Home" value="/" icon={<HomeIcon />} />
                <BottomNavigationAction label="Models" value="/models" icon={<MapIcon />} />
                <BottomNavigationAction component={Link} label="State" to="/state" icon={<SwapHorizIcon />} />
                <BottomNavigationAction component={Link} label="Editor" to="/editor" icon={<EditIcon />} />
                <BottomNavigationAction label="Palette" value="/palette" icon={<PaletteIcon />} />
            </BottomNavigation>
        </AppBar>
    );
}